.header{
    background-color: #344532;
    color: white;
    .profileImage{
        position: relative;
        top: 15px;
        left: 15px;
        border: 2px solid white;
        border-radius: 50px;
    }
}

.attendanceRate {
    color: #7dc24b;
    font-weight: 500;
}
.profileDetails{
    background-color: #FFFBEE;
    border: 1px solid #d8d2c0;
    .react-datepicker-wrapper input{
        border: 1px solid #3FAFE2;
        padding: 5px;
    }
    .dateCalendar{
        position: relative;
        left: -30px;
        color: #3FAFE2;
        cursor: pointer;
    }
}
#talkbubble {
    width: 150px;
    height: 40px;
    background: red;
    position: relative;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
    border-radius: 10px;
  }
  #talkbubble:before {
    content: "";
    position: absolute;
    right: 100%;
    top: 7px;
    border-top: 13px solid transparent;
    border-right: 20px solid red;
    border-bottom: 13px solid transparent;
  }
.ptoCircle{
    background-color: #37BEDE;
    display: inline-block;
    color: white;
    height: 30px;
    width: 30px;
    border-radius: 50%;
}
.fs25{
    font-size: 25px;
}

.height35{
    height: 35px;
}
